<template>
	<view class="integral">
		<!-- 背景图 -->
		<img class="integralBg" src="/static/integral_bg.png" />
		<!-- 分享给好友弹出的模态窗 -->
		<view class="modal" v-if="show" mode='aspectFill'>
			<view class="mtk" >
				<view class="mtks">
					<img class="mtksBg" src="/static/share.png" />
					好友点击进入后助力成功<br />一天内同一个好友只能助力一次哦
				</view>
				<button @click="closeGuild" open-type="share">OK, 了解</button>
			</view>
		</view>
		<!-- 解锁学习包模态窗 -->
		<view class="model" v-if="showLock">
			<view class="models">
				<view class="closeModel" @click="closeAudition">×</view>
				<view class="modelTitle">解锁学习包</view>
				<view class="row" v-for="(a,b) in lockList" :key="b">
					<view class="rowLeft">
						<img :src="a.icon" />
						<view>{{a.title}}</view>
					</view>
					<view class="rowRight" @click="clearLock(a)">解锁</view>
				</view>
			</view>
		</view>
		<!-- 积分剩余 -->
		<view class="residualIntegral">
			<view class="h2">{{data.integral || 0}}</view>
			<text>剩余积分</text>
		</view>
		<!-- 收集指南 -->
		<view class="content">
			<view class="collectionGuide">
				<view class="h4">收集指南</view>
			</view>
			<view class="guide guideOne overflow">
				<view class="left">
					<view class="h5">成为会员<text>热门</text></view>
					<text>+无限积分</text>
				</view>
				<!-- <button class="right" @click="toInTwo">查看详情</button> -->
				<button class="right" open-type="contact">查看详情</button>
			</view>
			<view class="guide guideTwo overflow">
				<view class="left">
					<view class="h5">每天登录</view>
					<text>+1积分</text>
				</view>
				<button class="right received" v-if="data.every_login=='received'">已领取</button>
				<button class="right incomplete" v-else>未完成</button>
			</view>
			<view class="guide guideThree overflow">
				<view class="left">
					<view class="h5">完成每天刷题</view>
					<text>+1积分</text>
				</view>
				<button class="right received" v-if="data.every_brush=='received'">已领取</button>
				<button class="right incomplete" v-else>未完成</button>
			</view>
			<view class="guide guideThree overflow">
				<view class="left">
					<view class="h5">连续刷3天题</view>
					<text>+5积分</text>
				</view>
				<button class="right received" v-if="data.every_three_brush=='received'">已领取</button>
				<button class="right incomplete" v-else>未完成</button>
			</view>
			<view class="guide guideFive overflow">
				<view class="left">
					<view class="h5">推荐给好友</view>
					<text>+20积分</text>
				</view>
				<button class="right" @click="goShare">去分享</button>
			</view>
			<view class="guide guideOne overflow">
				<view class="left">
					<view class="h5">解锁学习包</view>
					<text>-50积分</text>
				</view>
				<button class="right" @click="openLock">点击解锁</button>
			</view>
			<view class="guide guideOne overflow">
				<view class="left">
					<view class="h5">数据库查询</view>
					<text>-100积分（7天）</text>
				</view>
				<button class="right" open-type="contact">联系客服</button>
			</view>
			<view class="guide guideOne overflow">
				<view class="left">
					<view class="h5">择校报告</view>
					<text>-200积分/2份</text>
				</view>
				<button class="right" open-type="contact">联系客服</button>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { integral, lockList, unLock } from '@/common/http.api.js';
	export default {
		data() {
			return {
				show: false,
				showLock:false,
				isAndroid: false,
				student_id:'', 
				lockList:{},
				data:{}
			}
		},
		onShareAppMessage() {
			wx.showShareMenu({
		      withShareTicket: true,
		      menus: ['shareAppMessage', 'shareTimeline']
		     })
			let avatarUrl = store.state.vuex_user.avatar
			let nickName = store.state.vuex_user.nickname
		    return {
			  title: '考研英语长难句分析小程序',
		      path: '/pages/index/index?student_id='+this.student_id+'&avatarUrl='+avatarUrl+'&nickName='+nickName,
			  imageUrl:'/static/langStatic/shareBg.png',
		      // promise 
		    }
		},
		onShareTimeline: function () {
			return {
			  title: '考研英语长难句分析小程序',
			  imageUrl: '/static/langStatic/shareBg.png'
			}
		},
		onLoad() {
			if(this.$u.utils.isAndroid()) {
				this.isAndroid = true
			}
			// this.data.integral = uni.getStorageSync('integral')
			this.token = store.state.vuex_token
			integral({ token:this.token }).then(res => {
				console.log(res)
				this.data = res.data.data
				this.student_id = res.data.data.student_id
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 打开解锁学习包窗口
			openLock() {
				lockList({token:this.token}).then(res=>{
					console.log(res)
					this.lockList = res.data.data
					this.showLock = true
				}).catch(err=>{
					console.log(err)
				})
			},
			// 关闭解锁学习包窗口
			closeAudition() {
				this.showLock = false
			},
			//解锁
			clearLock(item) {
				unLock({token:this.token, type:item.type}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}else{
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			closeGuild() {
				this.show = false;
				
				/* uni.setNavigationBarColor({
					frontColor: "#000000",
					backgroundColor: "#ffd75c",
				}); */
			},
			goShare() {
				this.show = true
			},
			sure() {
				this.show = false
				const student_id = uni.getStorageSync('student_id')
				
			},
			toInTwo() {
				uni.redirectTo({
					url: "../center/infiniteIntegralTwo"
				})
			}
		}
	}
</script>

<style lang="less">
	
	.model{
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 5;
		.models{
			width: 90%;
			left: 50%;
			right: 50%;
			position: fixed;
			margin-top: 65%;
			border-radius: 20rpx;
			padding: 10rpx 0;
			padding-bottom: 50rpx;
			background-color: #fff;
			transform: translate(-50%,-50%);
			text-align: center;
			.closeModel{
				top: -25rpx;
				right: -25rpx;
				width: 50rpx;
				height: 50rpx;
				line-height: 50rpx;
				color: #949494;
				font-size: 38rpx;
				text-align: center;
				position: absolute;
				border-radius: 50%;
				background-color: #e1e1e1;
			}
			.modelTitle{
				font-weight: 600;
				margin: 30rpx 0;
			}
		}
	}
	.row{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 20rpx;
		margin-top: 50rpx;
		.rowLeft{
			display: flex;
			align-items: center;
			font-size: 28rpx;
			img{
				width: 60rpx;
				height: 80rpx;
				margin-right: 20rpx;
				z-index: 5;
				display: block;
			}
		}
		.rowRight{
			width: 150rpx;
			line-height: 52rpx;
			font-size: 28rpx;
			text-align: center;
			color: #fff;
			background: linear-gradient(to right, #ffd23e, #fca61d);
			border-radius: 100rpx;
		}
	}
	.modal{
		position: fixed;
		z-index: 999;
		background-color: rgba(0,0,0,.6);
	    top: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 100vh;
	}
	.mtk{			
		overflow: hidden;
	    width: 500rpx;
	    height: 500rpx;
	    position: fixed;
		border-radius: 40rpx;
	    z-index: 999;
		background-color: #fff;
		left: 50%;
		right: 50%;
		bottom: 10%;
		transform: translate(-50%,-50%);
		margin:auto;
	    transition: opacity 400ms ease-in;
		.mtks{
			.mtksBg{
				position: fixed;
				z-index: -1;
				width: 100%;
				left: 0;
				top: 0;
			}
			color: black;
			font-size: 28rpx;
			text-align: center;
			line-height: 40rpx;
			width: 500rpx;
			height: 110rpx;
			padding-top: 300rpx;
			border-top-left-radius: 40rpx;
			border-top-right-radius: 40rpx;
			border-bottom-left-radius: 0rpx;
			border-bottom-right-radius: 0rpx;
		}
		button{
			background-color: #fecf3d;
			color: #fff;
			font-size: 30rpx;
			font-weight: 600;
			line-height: 92rpx;
			letter-spacing: 4rpx;
			border-radius: 0;
			border-bottom-left-radius: 40rpx;
			border-bottom-right-radius: 40rpx;
		}
	}
	.integral {
		.integralBg{
			position: fixed;
			z-index: -1;
			top: 0;
			left: 0;
			width: 100%;
			margin-top: -50rpx;
		}
		position: relative;
		padding: 50rpx 40rpx;
		padding-bottom: 100rpx;
		.residualIntegral {
			text-align: center;
			color: #fff;
			margin-bottom: 50rpx;

			.h2 {
				font-size: 80rpx;
				margin-bottom: -10rpx;
			}

			text {
				font-size: 24rpx;
				font-weight: 200;
			}
		}

		.content {
			position: absolute;
			width: 85%;
			background-color: #fff;
			border-radius: 20rpx;
			padding: 20rpx;
			margin-top: 80rpx;
			button {
				width: 180rpx;
			}

			button:after {
				content: none;
			}

			.collectionGuide {
				.h4 {
					font-weight: 600;
					font-size: 32rpx;
					padding-left: 40rpx;
					background: url(../../static/point.png) no-repeat;
					background-size: 20rpx;
					background-position: left center;
					margin-top: 20rpx;
					margin-bottom: 60rpx;
				}
			}

			.guide {
				margin-bottom: 50rpx;

				.h5 {
					font-size: 32rpx;
					font-weight: 400;
					text {
						padding: 4rpx 12rpx;
						color: #fff;
						background-color: #f0744f;
						margin-left: 20rpx;
						border-radius: 6rpx;
						font-size: 20rpx;
					}
				}

				text {
					font-size: 28rpx;
					color: #f0744f;
				}

				button {
					width: 170rpx;
					border-radius: 100rpx;
					font-size: 28rpx;
					color: #fff;
				}
			}
			.received{
				background-color: #ffe491;
			}
			.incomplete{
				background-color: #d3d3d3;
			}
			.guideOne {

				button {
					background-color: #ffcd2f;
				}
			}

			.guideFive {
				button {
					background-color: #ffcd2f;
				}
			}
		}
	}
</style>
